<template>
  <div id="app">
    <div class="page">
      <!--头部-->
      <app-header></app-header>
      <!--导航-->
      <app-nav></app-nav>
      <!--路由-->
      <keep-alive>
        <router-view class="fadeView"></router-view>
      </keep-alive>
      <!--底部-->
      <app-footer></app-footer>
      <div class="round" @click="restart">home</div>
    </div>
    <app-menu></app-menu>
  </div>
</template>

<script>
  import sift from '@/components/sift'
  import header from '@/components/header'
  import nav from '@/components/nav'
  import footer from '@/components/footer'
  import menu from '@/components/menu'

  export default {
    name: 'app',
    components: {
      'app-sift': sift,
      'app-header': header,
      'app-nav': nav,
      'app-footer': footer,
      'app-menu': menu
    },
    mounted () {
      setTimeout(function () {
        $('.round').fadeToggle()
      }, 1300)
    },
    methods: {
      restart () {
        window.location.reload()
      }
    }
  }
</script>

<style>
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,body{
  width: 100%;
  height: 100%;
}
.page{
  z-index: 10;
  position: relative;
  top: 0;
  left: 0;
  transition: all 0.5s;
  background-color: #fff;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: .3rem;
}
.round{
  display: none;
  position: fixed;
  bottom: 2rem;
  right: .5rem;
  width: 1rem;
  height: 1rem;
  text-align: center;
  line-height: 1rem;
  color: #fff;
  border-radius: 50%;
  background-color: rgba(0,0,0,0.6);
  box-shadow: 0 0 20px #00a3ff;
  animation: blink_ .6s linear;
  -webkit-animation: blink_ .6s linear;
}
.round span{
  position: absolute;
  left: 12%;
  top: 12%;
  border-radius: 50%;
  display: block;
  width: .1rem;
  height: .1rem;
  background-color: #fff;
  -webkit-transform-origin: .4rem .4rem;
  -wevkit-animation: rod_ 2s linear infinite;
  animation: rod_ 2s linear  infinite ;
}
.toggle{
  transform: translateX(-2rem);
}
a{
  text-decoration: none;
  color: #000;
}
@-webkit-keyframes rod_ {
  from {
    -webkit-transform: rotate(0deg);
  } to {
      -webkit-transform: rotate(360deg);
    }
}
@keyframes rod_ {
  from {
    transform: rotate(0deg);
  } to {
      transform: rotate(360deg);
    }
}

@keyframes blink_ {
  0% {opacity: 0.4;}
  15% {opacity: 1;}
  30% {opacity: 0.5;}
  45% {opacity: 1;}
  60% {opacity: 0.6;}
  75% {opacity: 1;}
  90% {opacity: 0.7;}
  100% {opacity: 1;}
}
</style>




