<template>
  <div class="menu">
    <p>{{state.userName}}</p>
    <ul>
      <li v-for="m in state.menus"><a :href="menuSrc(m.path)">{{m.text}}</a></li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'menu',
    data () {
      return {
        state: {
          userName: '作品集',
          menus: [{
            text: '狂扁坏公鸡',
            path: 'http://top.51moshi.com/httpServer/html5/activity/ChickenGame.html?uid=20001'
          }, {
            text: '打雪仗',
            path: 'http://test.51moshi.com/httpServer/html5/activity/snowballGame.html?uid=20001'
          }, {
            text: '幸运大转盘',
            path: 'http://top.51moshi.com/httpServer/html5/turnplate/turnplate.html'
          }, {
            text: '代理充值',
            path: 'http://test.51moshi.com/httpServer/html5/agen_back/agency_login.html'
          }, {
            text: '红包排行榜',
            path: 'http://top.51moshi.com/httpServer/html5/share/red_ranking_list.html'
          }, {
            text: '帮助与反馈',
            path: 'http://top.51moshi.com/httpServer/html5/problem/problem_index.html'
          }, {
            text: '汽车大赛',
            path: 'http://top.51moshi.com/httpServer/html5/activity/shipGame.html'
          }, {
            text: '滑雪大赛',
            path: 'http://top.51moshi.com/httpServer/html5/activity/skiGame.html'
          }, {
            text: '相亲会',
            path: 'http://top.51moshi.com/httpServer/html5/activity/tanabataActivity.html'
          }, {
            text: '漂流瓶',
            path: 'http://top.51moshi.com/httpServer/html5/activity/driftBottleShare.html'
          }, {
            text: '感恩节',
            path: 'http://top.51moshi.com/httpServer/html5/activity/ThanksgivingDay.html'
          }]
        }
      }
    },
    methods: {
      menuSrc (src) {
        return src
      }
    }
  }
</script>

<style>
  .menu{
    display: none;
    z-index: 9;
    position: fixed;
    top: 0;
    right: 0;
    width: 2rem;
    height: 100%;
    background-color: #3c3c3d;
    color: #fff;
  }
  .menu>p{
    width: 100%;
    text-align: center;
    height: 1rem;
    line-height: 1rem;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    background-color: #000;
  }
  ul{
    list-style: none;
  }
  ul li{
    width: 100%;
    height: 1rem;
    text-align: center;
    line-height: 1rem;
  }
  ul li a{
    background-color: #3c3c3d;
    color: #fff;
  }
</style>



