import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import sift from '@/components/sift'
import gambit from '@/components/gambit'
import author from '@/components/author'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'nav-text-color',
  hashbang: true, // 将路径格式化为#!开头
  history: true, // 启用HTML5 history模式，可以使用pushState和replaceState来管理记录
  routes: [
    {
      path: '/sift',
      component: sift
    },
    {
      path: '/HelloWorld',
      component: HelloWorld
    },
    {
      path: '/gambit',
      component: gambit
    },
    {
      path: '/author',
      component: author
    },
    {
      path: '/*',
      component: sift
    }
  ]
})



