      <div v-for="m in state.author" class="author-item">
        <img :src="setAuthorSrc(m.src)" />
        <span>{{m.name}}</span>
        <p ref="dom">{{m.slogan}}</p>
        <div class="item-bottom">
          <span v-if="m.status" class="btn" @click="m.status = !m.status" ref="dom">关注</span>
          <span class="btned" v-else>已关注</span>
        </div>
      </div>
    <div class="clearfix"></div>
    </div>
</template>

<script>
  export default {
    name: 'author-box',
    data () {
      return {
        state: {
          author: [{
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '变革家',
            slogan: '帮股权投资者把好第一关！',
            status: true
          }, {
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '懂懂笔记',
            slogan: '20年国内财经媒体从业记录者！',
            status: true
          }, {
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '有道云',
            slogan: '开口说英语，学地道口语',
            status: true
          }, {
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '娱乐硬糖',
            slogan: '有温度的泛娱乐产业自媒体。！',
            status: true
          }],
          other: [{
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '邝新华',
            slogan: '新周刊主笔',
            status: true
          }, {
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '40秒',
            slogan: '认识最疯狂的天才',
            status: true
          }, {
            src: 'http://oss.51moshi.com/h5/activity/test.png',
            name: '艾问iAsk',
            slogan: '科技商业价值发现者',
            status: true
          }]
        }
      }
    },
    mounted () {
    },
    methods: {
      getData () {
      },
      setAuthorSrc (src) {
        return src
      }
    }
  }
</script>

<style>
  .author-box{
    width: 100%;
    height: 11.32rem;
    padding: 0 0 .2rem 0;
  }
  .clearfix:after {
    content: " ";
    display: block;
    clear: both;
    height: 0;
  }
  .author-box .author-item{
    float: left;
    position: relative;
    width: 45%;
    height: 4rem;
    margin-left: 3.5%;
    margin-top: .2rem;
    background-color: #262627;
    padding-top: .2rem;
    text-align: center;
  }
  .author-item img{
    display: block;
    margin: auto;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
  }
  .author-item>span{
    display: block;
    font-size: .3rem;
    color: #fff;
    padding-top: .1rem;
  }
  .author-item>p{
    width: 80%;
    margin-left: 10%;
    padding-top: .1rem;
    color: #666;
    font-size: .24rem;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .item-bottom{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: .7rem;
    padding-top: .15rem;
    background-color: #444;
  }
  .item-bottom span{
    display: block;
    width: 1.5rem;
    color: #fff;
    margin: auto;
    border-radius: .3rem;
  }
  .btn{
    background-color: #e94c3d;
  }
  .btned{
    background-color: #666;
  }
</style>



