<template>
  <div class="nav-box animate2">
    <div class="nav" v-for="m in menus">
      <router-link :to='setPaht(m.path)'>{{m.text}}</router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'nav',
    data () {
      return {
        // 路由参数
        menus: [{
          text: '精选',
          path: '/sift'
        },
        {
          text: '话题',
          path: '/gambit'
        },
        {
          text: '作者',
          path: '/author'
        }]
      }
    },
    mounted () {
      setTimeout(function () {
        $('.nav-box').removeClass('animate2')
      }, 300)
    },
    methods: {
      setPaht (path) {
        return path
      }
    }
  }
</script>

<style>
  .nav-box{
    display: flex;
    display: -webkit-flex;
    width: 100%;
    height: 1rem;
    line-height: 1rem;
    transition: all .7s;
  }
  .animate2{
    transform: translateX(100%);
  }
  .nav-box .nav{
    display: inline-block;
    width: 33.3%;
    text-align: center;
    font-size: .35rem;
    border-left: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
  }
  .nav>a{
    display: block;
    width: 100%;
    height: 100%;
    color: #666;
    text-decoration: none;
  }
  .nav-text-color{
    font-size: .35rem;
    background-color: #ebebeb;
  }
</style>



