<template>
  <div class="header animate">
    <div class="animation">
      <div><div></div></div>
      <div><div></div></div>
      <div><div></div></div>
      <div><div></div></div>
    </div>
    <h5>News</h5>
    <div class="aside" @click="toggle">
      <div v-for='i in 3' class="line"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'header',
    data () {
      return {
        menuBtn: true
      }
    },
    mounted () {
      setTimeout(function () {
        $('.header').removeClass('animate')
      }, 300)
    },
    methods: {
      toggle () {
        if (this.menuBtn === true) {
          $('.page').addClass('toggle')
          $('.aside').addClass('closeMenu')
          $('.menu').show()
          this.menuBtn = !this.menuBtn
        } else {
          $('.page').removeClass('toggle')
          $('.aside').removeClass('closeMenu')
          setTimeout(function () {
            $('.menu').hide()
          }, 500)
          this.menuBtn = !this.menuBtn
        }
        console.log(this.menuBtn)
      }
    }
  }
</script>

<style>
  .header{
    position: relative;
    display: flex;
    display: -webkit-flex;
    height: 1rem;
    background-color: #262627;
    transition: all .7s;
  }
  .animate{
    transform: translateX(-100%);
  }
  .animation{
    width: 1rem;
    height: 1rem;
    z-index: 999;
    position: absolute;
    top: 0;
    left: 25%;
    overflow: hidden;
  }
  .header h5{
    height: 1rem;
    font-size: .6rem;
    line-height: 1rem;
    text-indent: .2em;
    color: #fff;
  }
  .searchBtn{
    cursor: pointer;
    color: #FFFFFF;
    position: absolute;
    top: 18px;
    right: 15%;
  }
  .aside{
    height: .6rem;
    width: .6rem;
    cursor: pointer;
    position: absolute;
    top: .2rem;
    right: .2rem;
    padding-top: 1px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .line{
    height: 1px;
    width: 25px;
    background: #fff;
    transition: all 0.5s;
  }
  .line:nth-of-type(2){
    margin: 6px 0;
  }
  .closeMenu .line:first-child{
    transform: rotate(45deg);
  }
  .closeMenu .line:nth-child(2){
    display: none;
  }
  .closeMenu .line:last-child{
    transform: rotate(-45deg);
  }
  /*动画部分*/
  .animation > div {
    z-index: 999;
    position: absolute;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 1px solid #fff;
    -webkit-transform-style: preserve-3d;
    -webkit-transform: rotateX(80deg) rotateY(20deg);
    transform-style: preserve-3d;
    transform: rotateX(80deg) rotateY(20deg);
  }
  .animation > div:first-of-type:after {
    content: "";
    position: absolute;
    height: .4rem;
    width: .4rem;
    background: #fff;
    border-radius: 50%;
    -webkit-transform: rotateX(-80deg) rotateY(0);
    transform: rotateX(-80deg) rotateY(0);
    box-shadow: 0 0 25px #fff;
    -webkit-animation: nucleus_ 2s infinite linear;
    animation: nucleus_ 2s infinite linear;
    left: 65%;
    top: 50%;
    margin-top: -.2rem;
    margin-left: -.35rem;
  }
  .animation > div:nth-of-type(2) {
    -webkit-transform: rotateX(-80deg) rotateY(20deg);
    transform: rotateX(-80deg) rotateY(20deg);
  }
  .animation > div:nth-of-type(3) {
    -webkit-transform: rotateX(-70deg) rotateY(60deg);
    transform: rotateX(-70deg) rotateY(60deg);
  }
  .animation > div:nth-of-type(4) {
    -webkit-transform: rotateX(70deg) rotateY(60deg);
    transform: rotateX(70deg) rotateY(60deg);
  }
  .animation > div > div {
    width: 1rem;
    height: 1rem;
    position: relative;
    -webkit-transform-style: preserve-3d;
    -webkit-animation: trail_ 2s infinite linear;
    transform-style: preserve-3d;
    animation: trail_ 2s infinite linear;
  }
  .animation > div > div:after {
    content: "";
    position: absolute;
    top: -5px;
    box-shadow: 0 0 12px #fff;
    left: 50%;
    margin-left: -5px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #fff;
    -webkit-animation: particle_ 2s infinite linear;
    animation: particle_ 2s infinite linear;
  }
  .animation > div:nth-of-type(2) > div,
  .animation > div:nth-of-type(2) > div:after {
    -webkit-animation-delay: -.5s;
    animation-delay: -.5s;
  }
  .animation > div:nth-of-type(3)  > div,
  .animation > div:nth-of-type(3)  > div:after {
    -webkit-animation-delay: -1s;
    animation-delay: -1s;
  }
  .animation > div:nth-of-type(4) > div,
  .animation > div:nth-of-type(4) > div:after {
    -webkit-animation-delay: -1.5s;
    animation-delay: -1.5s;
  }
  @-webkit-keyframes trail_ {
    from {
      -webkit-transform: rotateZ(0deg);
    } to {
        -webkit-transform: rotateZ(360deg);
      }
  }
  @keyframes trail_ {
    from {
      transform: rotateZ(0deg);
    } to {
        transform: rotateZ(360deg);
      }
  }
  @-webkit-keyframes particle_ {
    from {
      -webkit-transform: rotateX(90deg) rotateY(0deg);
      transform: rotateX(90deg) rotateY(0deg);
    } to {
        -webkit-transform: rotateX(90deg) rotateY(-360deg);
        transform: rotateX(90deg) rotateY(-360deg);
      }
  }
  @keyframes particle_ {
    from {
      transform: rotateX(90deg) rotateY(0deg);
    } to {
        transform: rotateX(90deg) rotateY(-360deg);
      }
  }
  @keyframes nucleus_ {
    0% {
      box-shadow: 0 0 0 transparent;
    } 50% {
        box-shadow: 0 0 25px #fff;
      } 100% {
          box-shadow: 0 0 0 transparent;
        }
  }
  @-webkit-keyframes nucleus_ {
    0% {
      box-shadow: 0 0 0 transparent;
    } 50% {
        box-shadow: 0 0 25px #fff;
      } 100% {
          box-shadow: 0 0 0 transparent;
        }
  }
</style>



